// SECTION iteration :: START

.iteration {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  max-height: 100vh;

  &__slide {
    z-index: 5;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    max-height: 100vh;
    padding: 75px 100px;
    @include adaptive(1023) {
      padding-left: 50px;
      padding-right: 50px;
    }

    > div {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      max-height: 100%;

      img {
        max-height: 100%;
      }
    }

    &--image {
      img {
        z-index: 2;
        position: relative;
        display: block;
        max-width: 100%;
        height: auto;
        max-height: 100%;
        margin-left: auto;
        margin-right: auto;
      }
    }

    &--text {
      p {
        z-index: 2;
        position: relative;
        width: 100%;
        max-width: 898px;
        margin-left: auto;
        margin-right: auto;
        font-weight: bold;
        font-size: 52px;
        color: #9b9b9b;
        @include adaptive(1439) {
          font-size: 44px;
        }
        @include adaptive(1365) {
          font-size: 40px;
        }
        @include adaptive(1279) {
          font-size: 36px;
        }

        span {
          color: #000000;
        }
      }
    }

    &--5,
    &--27,
    &--33 {
      .iteration {
        &__bg {
          &-path {
            top: 50%;
            right: -28%;
            transform: translateY(-50%);
            width: 1298px;
            height: 1298px;
            border-radius: 50%;
            background-color: #fafafa;
            @include adaptive(1023) {
              left: 28%;
              right: auto;
            }
          }
        }
      }
    }

    &--6 {
      p {
        max-width: 1040px;
      }
    }

    &--8 {
      p {
        max-width: 760px;
      }
    }

    &--9,
    &--16 {
      .iteration {
        &__bg {
          &-path {
            top: 0;
            right: -55%;
            width: 1806px;
            height: 1020px;
            transform: skewX(-30deg);
            background-color: #f8fafc;
            @include adaptive(1023) {
              left: 55%;
              right: auto;
              width: 1206px;
            }
          }
        }
      }
    }

    &--10 {
      .iteration {
        &__bg {
          img {
            position: absolute;

            &:nth-of-type(1) {
              right: 220px;
              bottom: 250px;
            }

            &:nth-of-type(2) {
              right: -350px;
              bottom: -90px;
              @include adaptive(1365) {
                transform: scale(0.5);
              }
            }
          }
        }
      }
    }

    &--11 {
      p {
        max-width: 1040px;
      }
    }

    &--13,
    &--15,
    &--18,
    &--22,
    &--24,
    &--26,
    &--32 {
      p {
        max-width: 1315px;
      }
    }

    &--20 {
      p {
        max-width: 920px;
      }
    }

    &--27 {
      .iteration {
        &__bg {
          &-path {
            right: -20%;
          }
        }
      }
    }

    &--28 {
      p {
        max-width: 1180px;
      }
    }

    &--30 {
      p {
        max-width: 1420px;
      }
    }

    &--33 {
      .iteration {
        &__bg {
          &-path {
            left: 0;
            right: auto;
          }
        }
      }
    }

    &--34 {
      .iteration {
        &__bg {
          img {
            position: absolute;

            &:nth-of-type(1) {
              top: 100px;
              left: 100px;
            }

            &:nth-of-type(2) {
              right: -100px;
              bottom: -100px;
            }
          }
        }
      }
    }
  }

  &__bg {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;

    &-path {
      position: absolute;
    }
  }

  &__title {
    z-index: 0;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-weight: bold;
    font-size: 350px;
    line-height: 341px;
    color: #fafafa;
    @include adaptive(1534) {
      font-size: 300px;
      line-height: 300px;
    }
    @include adaptive(1439) {
      font-size: 250px;
      line-height: 250px;
    }
    @include adaptive(1335) {
      font-size: 200px;
      line-height: 190px;
    }
    @include adaptive(1279) {
      font-size: 150px;
      line-height: 140px;
    }
  }

  &__advantages {
    width: 100%;
    max-width: calc(100% / 3);

    &-wrapper {
      display: flex;
      flex-direction: row !important;
      justify-content: space-between;
      width: 100%;
      max-width: 1250px;
      height: auto !important;
      margin-left: auto;
      margin-right: auto;
    }

    &-img {
      min-height: 220px;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-text {
      width: 100%;
      max-width: 310px;
      padding-left: 35px;
      padding-top: 35px;
    }
  }

  &__search {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 710px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 525px;
      padding-bottom: 90px;
      font-weight: bold;
      font-size: 26px;
      color: #000;
    }

    &-img {
      padding-bottom: 50px;
      max-height: 100%;

      img {
        display: block;
        max-width: 100%;
        height: auto;
        margin-left: auto;
      }
    }

    &-desc {
      padding-left: 80px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__ai {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 710px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 525px;
      padding-bottom: 90px;
      font-weight: bold;
      font-size: 26px;
      color: #000;
    }

    &-badge {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      width: 100%;
      max-width: 630px;
      margin-left: auto;
      margin-right: auto;
      padding-bottom: 60px;

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 151px;
        height: 40px;
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 10px;
        padding: 0 20px;
        border-radius: 8px;
        background-color: #00449d;

        p {
          position: relative;
          top: -1px;
          font-family: 'Open Sans', sans-serif;
          padding-right: 20px;
          line-height: 1;
          font-size: 18px;
          color: #fff;
        }

        svg {
          width: 18px;
          height: 18px;
        }
      }
    }

    &-desc {
      padding-left: 80px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__column {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 610px;
    margin-left: auto;
    margin-right: auto;

    &-img {
      padding-bottom: 50px;
      max-height: 100%;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-text {
      padding-left: 100px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__pipelines {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 840px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 525px;
      padding-bottom: 40px;
      font-weight: bold;
      font-size: 26px;
      color: #000;
    }

    &-img {
      padding-bottom: 20px;
      max-height: 100%;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-desc {
      padding-left: 160px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__contacts {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 920px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      min-height: 67px;
      padding-left: 115px;
      padding-bottom: 25px;
      font-weight: bold;
      font-size: 30px;
      color: #000;
    }

    &-img {
      max-height: calc(100% - 67px);

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }

  &__schedule {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 630px;
    margin-left: auto;
    margin-right: auto;

    &-img {
      max-height: calc(100% - 84px);

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-desc {
      min-height: 84px;
      padding-left: 120px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__data {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 740px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      width: 100%;
      max-width: 450px;
      min-height: 136px;
      padding-bottom: 80px;
      font-weight: normal;
      font-size: 20px;
    }

    &-img {
      padding-left: 50px;
      max-height: calc(100% - 136px);

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
  }

  &__almanac {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 920px;
    margin-left: auto;
    margin-right: auto;

    &-img {
      max-height: calc(100% - 56px);

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-desc {
      min-height: 56px;
      padding-left: 120px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__flow {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 680px;
    margin-left: auto;
    margin-right: auto;

    &-img {
      max-height: 100%;

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-desc {
      padding-left: 120px;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__setting {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 1320px;
    margin-left: auto;
    margin-right: auto;

    &-img {
      max-height: calc(100% - 84px);

      img {
        display: block;
        max-width: 100%;
        height: auto;
      }
    }

    &-desc {
      position: relative;
      top: -80px;
      width: 100%;
      max-width: 640px;
      min-height: 84px;
      margin-left: auto;
      margin-right: auto;
      font-weight: normal;
      font-size: 20px;
      color: #848484;
    }
  }

  &__now {
    z-index: 2;
    position: relative;
    width: 100%;
    max-width: 640px;
    margin-left: auto;
    margin-right: auto;

    &-title {
      padding-bottom: 15px;
      font-weight: bold;
      font-size: 68px;
      color: #000;
      @include adaptive(1439) {
        font-size: 58px;
      }
      @include adaptive(1365) {
        font-size: 50px;
      }
      @include adaptive(1279) {
        font-size: 45px;
      }
    }

    &-desc {
      padding-bottom: 30px;
      font-weight: bold;
      font-size: 24px;
      color: #9b9b9b;
      @include adaptive(1365) {
        font-size: 22px;
      }
      @include adaptive(1279) {
        font-size: 20px;
      }
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 216px;
      height: 70px;
      border-radius: 10px;
      background-color: #00449d;
      text-transform: uppercase;
      font-weight: bold;
      font-size: 18px;
      letter-spacing: 0.08em;
      color: #fff;
      @include adaptive(1439) {
        height: 60px;
        font-size: 16px;
      }
      @include adaptive(1365) {
        height: 55px;
        letter-spacing: 0.04em;
        font-size: 16px;
      }
      @include adaptive(1365) {
        height: 50px;
        max-width: 200px;
        letter-spacing: 0.02em;
        font-size: 14px;
      }
    }
  }

  .swiper {
    &-container {
      opacity: 0;
      width: 100%;
      height: 100%;
      padding: 0;
    }

    &-slide {
      z-index: 0;
    }
  }
}

// SECTION iteration :: END